import React from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { StaticQuery, graphql } from 'gatsby';

import Header from './Header';
import Footer from './Footer';

const Layout = ({ children }) => (
  <StaticQuery
    query={graphql`
      query LayoutQuery {
        social: allSocialYaml {
          edges {
            node {
              name
              icon
              url
            }
          }
        }
        config: site {
          siteMetadata {
            fullName
            jobTitle
            title
            keywords
          }
        }
      }
    `}
    render={data => (
      <>
        <Helmet
          title={data.config.siteMetadata.title}
          meta={[
            { name: 'description', content: data.config.siteMetadata.title },
            { name: 'keywords', content: data.config.siteMetadata.keywords },
          ]}
          link={[
            {
              rel: 'stylesheet',
              crossorigin: 'anonymous',
              href:
                'https://cdnjs.cloudflare.com/ajax/libs/bulma/0.7.2/css/bulma.min.css',
              integrity: 'sha256-2pUeJf+y0ltRPSbKOeJh09ipQFYxUdct5nTY6GAXswA=',
            },
            {
              rel: 'stylesheet',
              crossorigin: 'anonymous',
              href: 'https://use.fontawesome.com/releases/v5.5.0/css/all.css',
              integrity:
                'sha384-B4dIYHKNBt8Bc12p+WXckhzcICo0wtJAoU8YZTY5qE0Id1GSseTk6S+L3BlXeVIU',
            },
          ]}
          htmlAttributes={{ lang: 'en' }}
        />
        <Header
          fullName={data.config.siteMetadata.fullName}
          jobTitle={data.config.siteMetadata.jobTitle}
        />
        {children}
        <Footer icons={data.social.edges} />
      </>
    )}
  />
);

Layout.propTypes = {
  children: PropTypes.node,
};

export default Layout;
